<template>
  <div class="loginBox">
    <div class="title">登录</div>
    <input class="sno" type="text" v-model="sno" @focus="snoFocus" @blur="snoBlur" />
    <input class="pwd" type="text" v-model="pwd" @focus="pwdFocus" @blur="pwdBlur" />
    <button>确定</button>
  </div>
</template>

<script>
export default {
  name: 'LoginBox',
  data () {
    return {
      sno: '学号',
      pwd: '密码',
      pwdBox: null
    };
  },
  methods: {
    snoFocus (e) {
      if (this.sno == '学号') {
        this.sno = '';
      }
    },
    snoBlur (e) {
      if (this.sno == '') {
        this.sno = '学号';
      }
    },
    pwdFocus (e) {
      if (this.pwd == '密码') {
        this.pwdBox.type = 'password';
        this.pwd = '';
      }
    },
    pwdBlur (e) {
      if (this.pwd == '') {
        this.pwdBox.type = 'text';
        this.pwd = '密码'
      }
    }
  },
  mounted () {
    this.pwdBox = document.querySelector('.loginBox .pwd');
  }
}
</script>

<style>
.loginBox {
  margin: 160px auto;
  width: 360px;
  height: 240px;
}

.loginBox .title {
  color: #fff;
  font-size: 32px;
  font-weight: 700;
  text-align: center;
}

.loginBox input {
  display: block;
  width: 240px;
  height: 36px;
  padding: 0px 10px;
  border: 0px;
  border-radius: 5px;
  margin: 20px auto;
  box-sizing: border-box;
  font-size: 16px;
  color: #fff;
  background-color: rgba(80, 80, 80, 0.3);
}

button {
  display: block;
  width: 240px;
  height: 36px;
  padding: 0px 10px;
  border: 0px;
  border-radius: 5px;
  margin: 20px auto;
  font-size: 16px;
  color: #eee;
  background-color: #f26934;
  outline: none;
  cursor: pointer;
}

button:hover {
  background-color: #f05418;
}
</style>


